import React, { useState } from 'react'
import { useEffect } from 'react';
import { useRef } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useNavigate } from 'react-router-dom';
import { registration } from '../../../http/actions/user';

export default function RegistrationForm() {
	const [email, setEmail] = useState("");
	const [password, setPassword] = useState("");
	const [surname, setSurname] = useState("");
	const [name, setName] = useState("");
	const emailRef = useRef();
	const { errorMessage, isAuth } = useSelector(state => state.user);
	const dispatch = useDispatch();
	const navigate = useNavigate();
	const goProfile = () => navigate('/profile/main');
	const goLogin = () => navigate('/login');

	useEffect(() => {
		emailRef.current.focus();
	}, [])

	useEffect(() => {
		if (isAuth) {

			goProfile()
		}
	}, [isAuth])

	const registrationFunc = () => {
		console.log("  = ", isAuth);
		dispatch(registration(email, password, surname, name));
	}

	return (
		<section className='registration-form '>
			<form onSubmit={e => e.preventDefault()} id='registrationform'>
				<h2 className='article__caption'></h2>
				<input
					ref={emailRef}
					onChange={e => setEmail(e.target.value)}
					value={email}
					name='email'
					type="email" placeholder=" "
					required />
				<input
					onChange={e => setPassword(e.target.value)}
					value={password}
					name='password'
					type="password" placeholder=""
					required />
				<input
					onChange={e => setSurname(e.target.value)}
					value={surname}
					name='surname'
					type="text" placeholder=""
					required />
				<input
					onChange={e => setName(e.target.value)}
					value={name}
					name='name'
					type="text" placeholder=""
					required />
				<button className='login-form__login' onClick={() => registrationFunc()}></button>
				{errorMessage && <div > {errorMessage}</div>}
				{/* {isAuth && goProfile()} */}
				<br />
				<button type='button' className='login-form__registration'
					onClick={() => goLogin()}>   </button>
			</form>
		</section>

	)
}
